.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: beige;
  min-height: 20vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.search-bar {
  display: flex;
  align-items: center;
}

.search-bar input[type="text"] {
  padding: 8px;
  margin-right: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.search-bar button {
  padding: 8px 16px;
  background-color: #aa4b14fa;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.navbar {
  background-color: #e0aa8bef;
  color: #fff;
  padding: 10px 20px; 
  
}

.nav-links {
  list-style-type: none;
  display: flex;
  justify-content: flex-end;
  
}

.nav-links li {
  margin-right: 20px; 
  position: relative;
  padding: 0 20;
}

.nav-links li a {
  color: #fff;
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: 600;
  transition: 0.3s ease-in-out;
}

.nav-links li a:hover,
.nav-links li a.active {
  color: #aa4b14fa;
}

.nav-links li a:hover::after,
.nav-links li a.active::after{
  content: "";
  width:30% ;
  height: 2px;
  background: #aa4b14fa;
  position: absolute;
  bottom: -4px;
  left: 20px;
}


.home-container h2 {
  text-align: center; 
  margin-bottom: 20px; 
}

.home-container {
  width: 100vw; /* Full viewport width */
  height: 100vh; /* Full viewport height */
  overflow: hidden; /* Hide overflow to prevent scrolling */
}

/* Styles for react-slick slider */
.slick-slider {
  width: 100px; /* Full width of the slider container */
  height: 100px; /* Full height of the slider container */
  margin: 5; /* Remove any margin */
  padding: 10; /* Remove any padding */
}

.slick-slide {
  width: 100px; /* Full width of each slide */
  height: 100px; /* Full height of each slide */
  position: relative; /* Ensure slides are positioned correctly */
}

.slick-slide img {
  width: 1200px; /* Ensure images fill their slide container */
  height: 1200px; /* Ensure images fill their slide container */
  object-fit: cover; /* Ensure images cover the slide */
  object-position: center; /* Center align images within the slide */
}
